import { useEffect } from 'react';
import { useDispatch } from 'react-redux';
import { auth, createUserProfileDocument } from '@app/firebase/firebase.utils';
import { shopSetCurrentUser } from '@app/old_store/actions';

export default function useShopLogin() {
    const dispatch = useDispatch();

    useEffect(() => {
        let onSnapshotSubscription;

        //   sign in  ,    
        const authSubscription = auth.onAuthStateChanged(async user => {
            //     user   null  sign out
            if (user) {
                const userRef = await createUserProfileDocument(user);

                //    userRef  
                // const snapshot = await userRef.get();
                // console.log(snapshot.data());

                //   ,   -  snapshot  DocumentReference  CollectionReference
                //    
                onSnapshotSubscription = userRef.onSnapshot(snapshot => {
                    //  mapDispatchToProps
                    dispatch(
                        shopSetCurrentUser({
                            id: snapshot.id,
                            ...snapshot.data(),
                        })
                    );
                });
            } else {
                dispatch(shopSetCurrentUser(false));
            }
        });

        return () => {
            // unsubscribe
            authSubscription();

            if (onSnapshotSubscription) {
                onSnapshotSubscription();
            }
        };
    }, [dispatch]);
}
